export default {

	data() {
		return {
			array: ['美国', '中国', '巴西', '日本'],
			isShow: false,
			listData:[]

		};
	},
	// 页面初次渲染
	onReady() {
		/* 种植中批次 */
		this.initData()
	},
	methods: {
		initData() {
			this.$api.getPagingCriteriaQuery({
				plantingPlanId: '',
				code: '',
				pageNo: 1,
				baseId: 23,
				plantingBatchStatus: 2

			}).then(res => {
				this.listData  = res.data.data.data
			})

		},
		downSelect() {
			this.isShow = !this.isShow
		},
		selectedFun(item) {

		}
	}
}
